export default "<template>\r\n    <div>\r\n        <raw-loader :mytxt=\"require('!raw-loader!./TplCtrlV0.vue').default\"></raw-loader>\r\n        <h1 class=\"border-bottom\">VueCrtV1</h1>\r\n        <p>Paste your image here..</p>\r\n        <img id=\"container\" /><br>\r\n        <img v-if=\" src!=null \" :src=\"src\" width=\"100\" height=\"80\" class=\"border\" />\r\n    </div>\r\n</template>\r\n  \r\n<script>\r\nexport default {\r\n    components: {\r\n    },\r\n    data() {\r\n        return {\r\n            src:null\r\n        };\r\n    },\r\n    mounted() {\r\n        document.addEventListener('paste', this.onPasteEvent)\r\n    },\r\n    methods: {\r\n        onPasteEvent(pasteEvent){\r\n            var item = pasteEvent.clipboardData.items[0];\r\n            let that=this;\r\n            if (item.type.indexOf(\"image\") === 0) {\r\n                var blob = item.getAsFile();\r\n                var reader = new FileReader();\r\n                reader.onload = function (event) {\r\n                    document.getElementById(\"container\").src = event.target.result;\r\n                    that.src=event.target.result;\r\n                };\r\n                reader.readAsDataURL(blob);\r\n            }\r\n        }\r\n    }\r\n\r\n}\r\n</script>\r\n  ";